import React, { useState } from 'react';
import './App.css';

function Todo({ todo, index, completeTodo, removeTodo}) {
  return(
    <div className='todoContainer'>
      <div className='todo'>
        <div className='completeTodoOuterContainer'>
          <div className='completeTodoContainer'>
            <button onClick={() => completeTodo(index)} className='completeTodo'>Done</button>
            <button onClick={() => completeTodo(index)} className='completeTodo2'>Done</button>
          </div>
        </div>
        <div className='todoTextRemoveTodoContainer'>
          <div className='todoText' style={{textDecoration: todo.isCompleted ? 'line-through' : ''}}>
            {todo.text}
          </div>
          <button onClick={() => removeTodo(index)} className='removeTodo'>x</button>
        </div>
      </div>
    </div>
  )
}

function TodoForm({addTodo}) {
  const [value, setValue] = useState('');

  const handleSubmit = e => {
    e.preventDefault();
    if(!value) return;
    addTodo(value);
    setValue('');
  }

  return (
    <form onSubmit={handleSubmit}>
      <input 
        type="text" 
        className="input" 
        value={value} 
        placeholder="Add Task, press 'Enter'..."
        onChange={e => setValue (e.target.value)}
      />
    </form>
  )
}

function App() {
  const [todos, setTodos] = useState([
    {
      text: 'complete Task 1',
      isCompleted: false
    },
    {
      text: 'work on Task 2',
      isCompleted: false
    },
    {
      text: 'do Task 3',
      isCompleted: false
    }
  ]);

  const addTodo = text => {
    const newTodos = [...todos, { text }];
    setTodos(newTodos);
  }

  const completeTodo = index => {
    const newTodos = [...todos];
    if (newTodos[index].isCompleted === true) {
      newTodos[index].isCompleted = false;
    } else {
      newTodos[index].isCompleted = true;
    };
    setTodos(newTodos);
  }

  const removeTodo = index => {
    const newTodos = [...todos];
    newTodos.splice(index, 1);
    setTodos(newTodos);
  }

  return (
    <div className="app">
      <div className='titleContainer'>
        <h1 className='title2'>?TA=SK!</h1>
        <h1 >?TA=SK!</h1>
      </div>
      <div className="todo-list">
        {todos.map((todo, index) => (
          <Todo 
            key={index} 
            index={index} 
            todo={todo} 
            completeTodo={completeTodo}
            removeTodo={removeTodo}
          />
        ))}
        <TodoForm addTodo={addTodo}/>
      </div>
    </div>
  )
}

export default App;